import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Button, Card, Col, Container, Form, Modal, Row, Table } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  const [validated, setValidated] = useState(false)
  const [firstName, setFirstname] = useState('')
  const [lastName, setLastname] = useState('')
  const [mobile, setMobile] = useState('')
  const [email, setEmail] = useState('')
  const [message, setMessage] = useState('')
  const [complaintTitle, setComplaintTitle] = useState('')
  const [complaintImage, setComplaintImage] = useState('')
  const [complaintsHistory, setComplaintsHistory] = useState([])
  const [show, setShow] = useState(false)

  const handleClose = () => setShow(false)
  const handleShow = () => setShow(true)

  const handleComplain = async (event) => {
    try {
      const form = event.currentTarget
      if (form.checkValidity() === false) {
        event.preventDefault()
        event.stopPropagation()
      }
      event.preventDefault()
      event.stopPropagation()
      const formData = new FormData()
      formData.append('userId', user.uniqueId)
      formData.append('complaintTitle', complaintTitle)
      formData.append('complaintMessage', message)
      formData.append('mobile', mobile)
      formData.append('firstName', firstName)
      formData.append('lastName', lastName)
      formData.append('email', email)
      formData.append('screenshot', complaintImage)

      const res = await axios.post('/api/user/add-complaint', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      })
      if (res.data.success) {
        setFirstname('')
        setLastname('')
        setMobile('')
        setEmail('')
        setMessage('')
        setComplaintTitle('')
        setComplaintImage('')
        message.succes('Complaint Created Successfully')
      }
    } catch (error) {
      console.log(error)
    } finally {
      setValidated(false)
    }
  }
  const convertToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader()
      fileReader.readAsDataURL(file)
      fileReader.onload = () => {
        resolve(fileReader.result)
        setComplaintImage(fileReader.result)
      }
      fileReader.onerror = (error) => {
        reject(error)
      }
    })
  }

  const getComplaints = async () => {
    try {
      const data = {
        userId: user.uniqueId,
      }
      const res = await axios.post(`/api/v1/user/getComplaints`, data)
      if (res.data.success) {
        console.log(res.data)
        setComplaintsHistory(res.data.data)
      }
      else{
        message.error('Something went wrong')
      }

      console.log(res.data)
    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    getComplaints()
  }, [])

  return (
    <>
      <Container className="mb-3">
        <Container>
          <Card>
            <Card.Body>
              <Table responsive>
                <thead>
                  <tr>
                    <td className="text-center">
                      <b>Sn</b>
                    </td>
                    <td className="text-center">
                      <b>Ticket No.</b>
                    </td>
                    <td className="text-center">
                      <b>Complaint Title</b>
                    </td>
                    <td className="w-50">
                      <b>Complaint Message</b>
                    </td>
                    <td className="text-center">
                      <b>Status</b>
                    </td>
                    <td className="text-center">
                      <b>Action</b>
                    </td>
                  </tr>
                </thead>
                <tbody>
                  {complaintsHistory && complaintsHistory.length > 0 ? (
                    <>
                      <tr>
                        <td className="text-center">1</td>
                        <td className="text-center">123456789</td>
                        <td className="text-center">Complaint Title</td>
                        <td className="">Complaint Message..</td>
                        <td className="text-center">Open</td>
                        <td className="text-center">
                          <Button onClick={handleShow} className="myCustomBtn">
                            View More
                          </Button>
                        </td>
                      </tr>
                      <Modal show={show} onHide={handleClose} animation={false}>
                        <Modal.Header closeButton>
                          <Modal.Title>Modal heading</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>Woohoo, you are reading this text in a modal!</Modal.Body>
                        <Modal.Footer>
                          <Button variant="secondary" onClick={handleClose}>
                            Close
                          </Button>
                          <Button variant="primary" onClick={handleClose}>
                            Save Changes
                          </Button>
                        </Modal.Footer>
                      </Modal>
                    </>
                  ) : (
                    <tr>
                      <td colSpan="6" className="text-center text-danger">
                        No Complaints Found
                      </td>
                    </tr>
                  )}
                </tbody>
              </Table>
            </Card.Body>
          </Card>
        </Container>
      </Container>
      <Container fluid className="d-flex flex-column-reverse flex-md-row">
        <Container className="col-12 col-md-8 mt-3 mt-md-0">
          <Card>
            <Card.Header>
              <div className="">Complaint Form</div>
            </Card.Header>
            <Card.Body>
              <Form noValidate validated={validated} onSubmit={handleComplain} autoComplete="off">
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Firts Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter First Name"
                      onChange={(e) => {
                        setFirstname(e.target.value)
                      }}
                      value={firstName}
                      required
                    />
                  </Form.Group>
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Last Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Last Name"
                      onChange={(e) => {
                        setLastname(e.target.value)
                      }}
                      value={lastName}
                      required
                    />
                  </Form.Group>
                </Row>
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Unique Id</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Mobile Number"
                      value={user && user.uniqueId}
                      disabled
                    />
                  </Form.Group>

                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Email"
                      onChange={(e) => {
                        setEmail(e.target.value)
                      }}
                      value={email}
                      required
                    />
                  </Form.Group>
                </Row>
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Mobile Number</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Mobile Number"
                      onChange={(e) => {
                        setMobile(e.target.value)
                      }}
                      value={mobile}
                      required
                    />
                  </Form.Group>
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>
                      Add a ScreenShot <span className="text-danger">*Note if neccessary</span>
                    </Form.Label>

                    <Form.Control
                      type="file"
                      placeholder="Enter Mobile Number"
                      onChange={(e) => {
                        convertToBase64(e.target.files[0])
                      }}
                      value={mobile}
                      required
                    />
                  </Form.Group>
                </Row>
                <div className="mb-3 d-flex flex-column-reverse flex-md-row">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Complaint Title</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Complaint Title"
                      onChange={(e) => {
                        setComplaintTitle(e.target.value)
                      }}
                      value={complaintTitle}
                      required
                    />
                  </Form.Group>
                  <Container className="mb-3 mb-md-0">
                    <div className="d-flex">
                      {complaintImage && (
                        <>
                          <p className="me-3">Preview</p>
                          <div
                            className="d-flex"
                            style={{
                              background: '#041215',
                            }}
                          >
                            <img
                              src={complaintImage}
                              style={{
                                height: '100px',
                                width: '100px',
                                objectFit: 'contain',
                              }}
                              alt=""
                            />
                          </div>
                        </>
                      )}
                    </div>
                  </Container>
                </div>
                <Row className="mb-3">
                  <Form.Group as={Col} className="" controlId="formName">
                    <Form.Label>Complaint Message</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={3}
                      placeholder="Enter Complaint Message"
                      onChange={(e) => {
                        setMessage(e.target.value)
                      }}
                      value={message}
                      required
                    />
                  </Form.Group>
                </Row>
                <div className="d-flex justify-content-end">
                  <Button className="myCustomBtn" type="submit">
                    Submit
                  </Button>
                </div>
              </Form>
            </Card.Body>
          </Card>
        </Container>
        <Container className="col-12 col-md-4">
          <Card>
            <Card.Header>Contact Details</Card.Header>
            <Container className="d-flex flex-column align-items-center justify-content-between">
              <table className="m-3">
                <tr>
                  <td>
                    <i
                      style={{ background: '#4175fc' }}
                      className="fa-solid fa-phone p-2 rounded-5 text-white"
                    ></i>
                  </td>
                  <td>
                    <Link
                      style={{
                        textDecoration: 'none',
                        whiteSpace: 'nowrap',
                      }}
                      className="text-black ms-3"
                      to="tel:+91 - 9588285510"
                    >
                      +91 - 9588285510
                    </Link>
                  </td>
                </tr>
                <br />
                <tr>
                  <td>
                    <i
                      style={{ background: '#4175fc' }}
                      className="fa-solid fa-envelope p-2 rounded-5 text-white"
                    ></i>
                  </td>

                  <td>
                    <Link
                      style={{
                        textDecoration: 'none',
                      }}
                      className="text-black"
                      to="mailto:care.adinaths@gmail.com"
                      onClick={() => {
                        window.location.href = 'mailto:care.adinaths@gmail.com'
                      }}
                    >
                      care.adinaths@gmail.com
                    </Link>
                  </td>
                </tr>
                <br />
                <tr>
                  <td>
                    {' '}
                    <i
                      style={{ background: '#4175fc', whiteSpace: 'nowrap' }}
                      className="fa-solid fa-location-dot p-2 rounded-5 text-white"
                    ></i>
                  </td>
                  <td>Near ICICI Bank Varda Sagwara Dungarpur Rajasthan 314029</td>
                </tr>
              </table>
            </Container>
          </Card>
        </Container>
      </Container>
    </>
  )
}
export default Index

